import React, {useEffect, useState} from 'react';
import './App.module.css';
import {MuiThemeProvider} from "@material-ui/core";
import {theme} from "../utils/themeMaterialUI";
import {Header} from '../components/Header/Header';
import {Menu} from "../components/Menu/Menu";
import styles from "./App.module.css"
import {Footer} from "../components/Footer/Footer";
import {Routes} from "../components/Routes/Routes";
import TransitionsModal from "../components/Modal/Modal";

export const App: React.FC = React.memo(() => {

        let timeoutId: number | NodeJS.Timeout | null | undefined = null
        const [isSleep, setIsSleep] = useState<boolean>(false)
        useEffect(() => {
        })

        const setHibernation = () => {
            if (timeoutId !== null) {
                // @ts-ignore
                window.clearTimeout(timeoutId)
                setIsSleep(false)
                console.log("wake up")
            }
            timeoutId = setTimeout(() => {
                setIsSleep(true)
                console.log("sleep")
            }, 5000)
        }


        return (
            <div className={styles.App} /*onMouseOver={setHibernation}*/>
                <MuiThemeProvider theme={theme}>
                    <Header/>
                    <div className={styles.mainBlock}>
                        <Menu/>
                        <Routes/>
                    </div>
                    <Footer/>
                </MuiThemeProvider>
                {/*{isSleep && <TransitionsModal isSleep={isSleep}/>}*/}
            </div>
        )
    }
)

